/* Input styles */
.input_field{
    width: 300px;
    min-height: 60px;
    margin: 2vh 1vh;
    background: var(--inputs);
    border: 2px solid var(--secondary);
    border-radius: 2vh;
    display: flex;
    justify-content: left;
    transition: .6s ease-in-out;
}
.input_field:focus-within{
    border: 2px solid var(--principal);
}
.input_field:focus-within > .field_name{
    transform: translateX(20px);
    color: var(--principal);
}
.input_field > div{
    width: 100%;
    max-height: 100px;
}
.field_name{
    position: absolute;
    padding: 0 1vh;
    background: linear-gradient(var(--white) 50%, var(--inputs)70%);
    font-size: 1.1rem;
    font-weight: 800;
    color: var(--secondary);
    margin: -13px 3vh 0;
    border-radius: 1vh 1vh 0 0;
    transition: .6s ease-in-out;
}
.field_type_input{
    display: block;
    width: 100%;
    height: 56px;
    background: var(--inputs);
    color: var(--dark);
    border: none;
    outline: none;
    border-radius: 2vh;
    text-align: center;
    font-size: 1rem;
    padding: 0 2vh;
}
.field_type_textarea{
    min-width: 100%;
    max-width: 100%;
    min-height: 100px;
    max-height: 100px;
    background: var(--inputs);
    color: var(--dark);
    border: none;
    outline: none;
    border-radius: 2vh;
    text-align: center;
    font-size: 1rem;
    padding: 1vh 2vh;
}
.field_type_slider{
    width: 70px;
    min-width: 70px;
    max-width: 70px;
    height: 35px;
    margin: auto;
    background: #D9D9D9;
    position: relative;
    border-radius: 20px;
}
.field_type_slider:before{
    content: 'SI';
    color: var(--principal);
    position: absolute;
    left: 8px;
    top: 7px;
    font-size: 1rem;
    font-weight: 900;
}
.field_type_slider:after{
    content: 'NO';
    color: var(--dark);
    position: absolute;
    right: 8px;
    top: 7px;
    font-size: 1rem;
    font-weight: 900;    
}
.field_type_slider > label{
    display: block;
    width: 29px;
    height: 29px;
    cursor: pointer;
    position: absolute;
    top: 3px;
    left: 3px;
    z-index: 1;
    background: var(--dark);
    border-radius: 50px;
    transition: .6s ease-in-out;
}
.field_type_slider > input[type=checkbox]{
    visibility: hidden;
}
input[type=checkbox]:checked + label{
    background: var(--principal);
    left: 38px;
}
.input_field:has(.field_type_input[type="file"]){
    overflow: hidden;
    max-height: 60px;
}
.field_type_input[type="file"]::before {
    content: 'Seleccionar imagen';
    display: block;
    background: var(--principal);
    color: var(--inputs);
    padding: 1vh;
    margin: 5% auto 5vh;
    border-radius: 2vh;
    text-align: center;    
    cursor: pointer;
    position: relative;
}
.image_container{
    width: 250px;
    min-width: 250px;
    height: 250px;
    margin: 0 10vh 1vh;
    border: 3px dashed var(--dark);
    border-radius: 4vh;
    background-image: url('../assets/images/icons/Upload.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 120px;
    cursor: pointer;
}
.uploaded_image{
    display: block;
    width: 210px;
    height: 210px;
    margin: 17px;
    border-radius: 3vh;
}
.image_description{
    width: 250px;
    margin: 0 11vh 1vh;
    text-align: center;
    color: var(--dark);
    font-size: .85rem;
}
.form_inputs{
    width: 100%;
    max-width: 630px;
    margin: 1rem auto 3rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

/* Change default styles */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus{
  -webkit-box-shadow: 0 0 0px 1000px var(--inputs) inset !important;
  transition: background-color 5000s ease-in-out 0s !important;
}